  background-image: linear-gradient(to right, rgba(254, 154, 192, .94), rgba(254, 154, 192, .94)), url("alice.73f6aaa0.jpg");
}

form {
  gap: 8px;
  display: flex;
}

.gallery {
  flex-wrap: wrap;
  justify-content: center;
  gap: 5px;
  margin: 10px;
  display: flex;
}

input {
  border-radius: 8px;
  margin-left: auto;
}

.button-1 {
  box-sizing: border-box;
  color: #fff;
  cursor: pointer;
  height: 40px;
  text-align: center;
  vertical-align: baseline;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  touch-action: manipulation;
  background-color: #ea4c89;
  border-style: none;
  border-radius: 8px;
  outline: none;
  margin-right: auto;
  padding: 10px 16px;
  font-family: Haas Grot Text R Web, Helvetica Neue, Helvetica, Arial, sans-serif;
  font-size: 14px;
  font-weight: 500;
  line-height: 20px;
  text-decoration: none;
  list-style: none;
  transition: color .1s;
  display: inline-block;
  position: relative;
}

.button-1:hover, .button-1:focus {
  background-color: #f082ac;
}

.photo-card {
  background-color: pink;
  padding: 5px;
  transition: transform .2s;
  box-shadow: 4px 4px 4px 4px #855a6a;
}

.photo-card:hover {
  transform: scale(1.05);
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 3px solid #0e0c0c;
  border-radius: 50%;
  margin: 20px auto;
  animation: animloader 2s linear infinite;
  display: block;
  position: relative;
}

.loader:after {
  content: "";
  box-sizing: border-box;
  width: 6px;
  height: 24px;
  background: #0e0c0c;
  position: absolute;
  bottom: -20px;
  left: 46px;
  transform: rotate(-45deg);
}

@keyframes animloader {
  0% {
    transform: translate(-10px, -10px);
  }

  25% {
    transform: translate(-10px, 10px);
  }

  50% {
    transform: translate(10px, 10px);
  }

  75% {
    transform: translate(10px, -10px);
  }

  100% {
    transform: translate(-10px, -10px);
  }
}

